import React, { useState, useEffect } from 'react'
import './App.css';
import './fee.css';


function Fee() {
    const [ethLow, setEthLow] = useState([]);
    const [ethMedium, setEthMedium] = useState([]);
    const [ethHigh, setEthHigh] = useState([]);

    const [BtcLow, setBtcLow] = useState([]);
    const [BtcMedium, setBtcMedium] = useState([]);
    const [BtcHigh, setBtcHigh] = useState([]);

    const [EtherScanLow, setEtherScanLow] = useState([0]);
    const [EtherScanMedium, setEtherScanMedium] = useState([0]);
    const [EtherScanHigh, setEtherScanHigh] = useState([0]);

    const [hash, setHash] = useState(0);

    useEffect(() => {

        setInterval(function () {

            fetch('https://ethgasstation.info/api/ethgasAPI.json?api-key=XXAPI_Key_HereXXX')
                .then((res) => {
                    return res.json()
                })
                .then((data) => {
                    var gasS = data.safeLow;
                    var gasP = data.fast;
                    var gasF = data.fastest;
                    setEthLow(gasS);
                    setEthMedium(gasP);
                    setEthHigh(gasF);

                })

            fetch('https://api.etherscan.io/api?module=gastracker&action=gasoracle&apikey=YourApiKeyToken')
                .then((res) => {
                    return res.json()
                })
                .then((data) => {
                    setEtherScanLow(data.result.SafeGasPrice);
                    setEtherScanMedium(data.result.ProposeGasPrice);
                    setEtherScanHigh(data.result.FastGasPrice);


                })

            fetch('https://mempool.space/api/v1/fees/recommended')
                .then(res => res.json())
                .then(data => {
                    var BtcLow = data.hourFee;
                    var BtcMedium = data.halfHourFee;
                    var BtcHigh = data.fastestFee;
                    setBtcLow(BtcLow);
                    setBtcMedium(BtcMedium);
                    setBtcHigh(BtcHigh);
                })

            fetch('https://mempool.space/api/blocks/tip', {

                headers: [
                    ['Content-Type', 'application/csp-report'],
                    ['Content-Type', 'application/expect-ct-report+json'],
                    ['Content-Type', 'application/xss-auditor-report'],
                    ['Content-Type', 'application/ocsp-request'],
                ]
            })
                .then(res => res.json())
                .then(data => {
                    let time = (new Date((data[0].timestamp) * 1000)).toString();
                    console.log(time.slice(17, 25));
                    setHash(time.slice(17, 25));
                })

        }, 30000)

    }, [])

    return (
        <>
            <div className="container mb-4">
                <div className="row">
                    <div className="col-lg-6 mb-3 mb-sm-0">
                        <center>ETH Gwei</center>
                        <div className="card-body">
                            <div className="row text-center mb-3">
                                <div className="col-md-4">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">GasStation / EtherScan</p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthLow">{(ethLow / 10).toFixed(0)} / {EtherScanLow} gwei</span></span>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">GasStation / EtherScan</p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthMedium">{(ethMedium / 10).toFixed(0)} / {EtherScanMedium} gwei</span></span>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">GasStation / EtherScan</p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthHigh">{(ethHigh / 10).toFixed(0)} / {EtherScanHigh} gwei</span></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-lg-6 mb-3 mb-sm-0">
                        <center>BTC Fee</center>
                        <div className="card-body">
                            <div className="row text-center mb-3">
                                <div className="col-md-4">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">Low </p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthLow">{BtcLow} </span> </span>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">Medium </p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthMedium">{BtcMedium}</span></span>
                                    </div>
                                </div>
                                <div className="col-md-4">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">High </p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthHigh">{BtcHigh}</span> </span>
                                    </div>
                                </div>

                                <div className="col-md-12 lastBlock">
                                    <div className="card h-100 p-3 shadow-none">
                                        <h3 className="h6 mb-0"><p id="Eth">Last Block </p></h3>
                                        <span className="h4 text-primary mb-1"><span id="EthHigh">{hash}</span> </span>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>

    )
}

export default Fee
